import React, { Component } from 'react';

class Education extends Component {

    facultyName(edu)
    {
        if( typeof edu.url === 'string' )
            return <a href={edu.url} target="_blank">{edu.faculty}</a>
        else
            return edu.faculty
    }

    render() {
        let {edus} = this.props

        return (
            <div className="yui-gf last">
                <div className="yui-u first">
                    <h2>Education</h2>
                </div>
                <div className="yui-u">
                    {
                        edus.map((edu, i)=>{
                            return (
                                <div className="edu" key={i}>
                                    <h2>{edu.university}</h2>
                                    <h4>{edu.duration}</h4>
                                    <p>{edu.major}, {this.facultyName(edu)} &mdash; <strong>{edu.gpa} GPA</strong> </p>
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        )
    }
}

export default Education


