import React, { Component } from 'react';
import Profile from './body/Profile';
import Skills from './body/Skills';
//import Technical from './body/Technical';
import Experience from './body/Experience';
import Education from './body/Education';

class Body extends Component {
    render() {
        let {userData} = this.props

        return (
            <div id="bd">
                <div id="yui-main">
				    <div className="yui-b">
                        <Profile personal={userData.personal} />
                        <Skills skills={userData.skills} />
                        <Experience exps={userData.exps} />
                        <Education edus={userData.edus} />
                    </div>
                </div>
            </div>
        )
    }
}

export default Body


