import React, { Component } from 'react';

class Footer extends Component {
    render() {
        let {userData} = this.props

        return (
            <div id="ft">
                <p>{userData.firstname} {userData.lastname} &mdash; <a href={'mailto:' + userData.email}>{userData.email}</a> &mdash; {userData.mobile}</p>
            </div>
        )
    }
}

export default Footer


