import React, { Component } from 'react';

class Skills extends Component {
    render() {
        let {skills} = this.props

        return (
            <div id="skill" className="yui-gf">
                <div className="yui-u first">
                    <h2>Skills</h2>
                </div>
                <div className="yui-u skills">
                    {
                        skills.map((skill, i)=>{
                            return (
                            <div className="talent20 vcenter" key={i}>
                                <img src={require("../" + skill.img)} alt={skill.alt} width="70%" />
                            </div>
                            )
                        })
                    }
                </div>
            </div>
        )
    }
}

export default Skills


