import React, { Component } from 'react';

class Experience extends Component {

    companyName(exp)
    {
        if( typeof exp.url === 'string' )
            return <a href={exp.url} target="_blank">{exp.company}</a>
        else
            return exp.company
    }

    render() {
        let {exps} = this.props
        let countExp = exps.length

        return (
            <div className="yui-gf">

                <div className="yui-u first">
                    <h2>Experience</h2>
                </div>

                <div className="yui-u exp">
                    {
                        exps.map((exp, i)=>{
                            return (
                                <div className={(i === (countExp-1)) ? 'job last' : 'job'} key={i}>
                                    <h2>{this.companyName(exp)}</h2>
                                    <h3>{exp.position}</h3>
                                    <h4>{exp.duration}</h4>
                                    <ul>
                                    {
                                        exp.jd.map((job_description,ii) => <li key={ii}>{job_description}</li>)
                                    }
                                    </ul>
                                        
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        )
    }
}

export default Experience


