import React, { Component } from 'react';
import Header from './Header';
import Body from './Body';
import Footer from './Footer';
import './reset-fonts-grids.css';
import './resume.css';
import user from './data';


class App extends Component {

  constructor(props) {
    super(props);
    this.state = {lang: 'en'};
  }

  switchLang(e){
    this.setState({
      lang: e.target.alt
    })
  }

  render() {
    let {lang} = this.state
    return (
      <div id="paper">
        <div id="lang" className="lang">
          <img src={require("./img/th.png")} alt="th" onClick={(e)=>this.switchLang(e)}/>&nbsp;
          <img src={require("./img/en.png")} alt="en" onClick={(e)=>this.switchLang(e)}/>
        </div>
        <div id="resume">
          <div id="inner">
            <Header userData={user[lang]} />
            <Body userData={user[lang]} />
            <Footer userData={user[lang]} />
          </div>
        </div>
      </div>
    );
  }
}

export default App;



